* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
  background-color: rgb(5, 24, 24);
}

h1 {
  margin-top: 0;
  font-size: 22px;
}

h2 {
  margin-top: 0;
  font-size: 20px;
}

h3 {
  margin-top: 0;
  font-size: 18px;
}

h4 {
  margin-top: 0;
  font-size: 16px;
}

h5 {
  margin-top: 0;
  font-size: 14px;
}

h6 {
  margin-top: 0;
  font-size: 12px;
}

code {
  font-size: 1.2em;
}

ul {
  padding-left: 20px;
}

.wraper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.status {
  color: antiquewhite;
  margin-top: 50px;
  margin-bottom: 50px;
  font-size: 45px;
  font-weight: bold;
}

.square {
  background: rgb(0, 9, 3);
  color: antiquewhite;
  border: 2px solid white;
  float: left;
  font-size: 40px;
  font-weight: bold;
  line-height: 34px;
  height: 80px;
  width: 80px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  cursor: pointer;
}

.square:hover {
  background-color: rgb(5, 24, 24);
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.reset {
  margin: 40px;
}

.resetButton {
  background-color: antiquewhite;
  border: none;
  padding: 5px;
  cursor: pointer;
  border-radius: 5px;
  width: 80px;
  height: auto;
  font-size: large;
}

.resetButton:hover {
  background-color: lightpink;
}


